<template>
  <div class="Contact container-fluid" :class="this.$root.theme_bc_2">
    <div style="height:18px" />
    <div class="mainArea" style="text-align:left; margin:auto;"> 
      <div class="main">
        <div style="border-radius:6px; background-color:#ffffff; padding:14px 12px;">
          <div v-show="status == 0">
            
            <div class="grayBar" />
             <span class="constrains" :class="this.$root.theme_fc_1"></span>
            <p class="errMsg" v-if="errorMessageData.name"> {{errorMessageData.name}}</p>
            <input type="text" class="input-text" :class="[this.$root.theme_focus_1, this.$root.theme_bc_2, this.$root.theme_input_2]" v-model="name" style="width:100%" maxlength="50" placeholder="">

            <div class="sp_line_l" />

             <span class="constrains" :class="this.$root.theme_fc_1"></span>
            <p class="errMsg" v-if="errorMessageData.mailAddress"> {{errorMessageData.mailAddress}}</p>
            <input type="text" class="input-text" :class="[this.$root.theme_focus_1, this.$root.theme_bc_2, this.$root.theme_input_2]" v-model="mailAddress" style="width:100%" maxlength="50" placeholder="">

            <div class="sp_line_l" />

            
            <p class="errMsg" v-if="errorMessageData.phoneNumber"> {{errorMessageData.phoneNumber}}</p>
            <input type="text" class="input-text" :class="[this.$root.theme_focus_1, this.$root.theme_bc_2, this.$root.theme_input_2]" v-model="phoneNumber" style="width:100%" maxlength="50" placeholder="">

            <div class="sp_line_l" />

             <span class="constrains" :class="this.$root.theme_fc_1"></span>
            <p class="errMsg" v-if="errorMessageData.messageBody"> {{errorMessageData.messageBody}}</p>
            <textarea id="story" class="input-text" :class="[this.$root.theme_focus_1, this.$root.theme_bc_2, this.$root.theme_input_2]" v-model="messageBody" name="story" rows="10" style="height:auto; width:100%" maxlength="150"
             placeholder=""></textarea>

            <div class="sp_line_xl" />
            <span class="btn" :class="this.$root.theme_bc_1" style="width:160px" v-on:click="pushConfirm"></span>
          </div>
          <div v-show="status == 1">
            

            <div class="grayBar" />
            
            <div class="sp_line_s" />
            <div style="width:90%; margin:auto">
              {{name}}
            </div>
            <div class="sp_line_l" />

            
            <div class="sp_line_s" />
            <div style="width:90%; margin:auto">
              {{mailAddress}}
            </div>
            <div class="sp_line_l" />

            
            <div class="sp_line_s" />
            <div style="width:90%; margin:auto">
              {{phoneNumber}}
            </div>
            <div class="sp_line_l" />

            
            <div class="sp_line_s" />
            <div style="width:90%; margin:auto">
              {{messageBody}}
            </div>
            
            <div class="sp_line_xl" />
            
            <div class="sp_line_m" />

            <span class="btn" :class="this.$root.theme_bc_1" style="width:160px" v-on:click="pushSubmit"></span>
            <div class="sp_line_s" />
            <span class="btn btn-gray" style="width:160px" v-on:click="pushEdit"></span>

          </div>
          <div v-show="status == 2">
            
            <div class="grayBar" />
            <br>
            <br>
            

            <div style="height:25px" />
            <span class="btn" :class="this.$root.theme_bc_1" style="width:160px" v-on:click="pushTop">TOP</span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  name: 'Contact',
  components: {
  },
  data() {
    return {
      status : 0,

      name : "",
      mailAddress : "",
      phoneNumber : "",
      messageBody : "",

      errorMessageData : {
        name : "",
        mailAddress : "",
        phoneNumber : "",
        messageBody : "",
      }
    };
  },
  computed : {
  },
  methods : {   
    pushConfirm : function() {
      this.errorMessageData.name = "";
      this.errorMessageData.mailAddress = "";
      this.errorMessageData.phoneNumber = "";
      this.errorMessageData.messageBody = "";

      //
      this.name = this.name.trim();
      this.mailAddress = this.mailAddress.trim();
      this.phoneNumber = this.phoneNumber.trim();
      
      //
      if (this.name == "") {
        this.errorMessageData.name = "";
      } else if (this.name.length > this.$root.LENGTH_MAIL) {
        this.errorMessageData.name = "50";
      }
      
      if (this.mailAddress == "") {
        this.errorMessageData.mailAddress = "";
      } else if (this.mailAddress.length > this.$root.LENGTH_MAIL) {
        this.errorMessageData.mailAddress = "50";
      }
      
      if (this.phoneNumber.length > this.$root.LENGTH_MAIL) {
        this.errorMessageData.mailAddress = "50";
      }

      if (this.messageBody == "") {
        this.errorMessageData.messageBody = "";
      } else if (this.messageBody.length > this.$root.LENGTH_MAIL) {
        this.errorMessageData.messageBody = "50";
      }

      if (this.errorMessageData.name != "" || this.errorMessageData.mailAddress != "" || this.errorMessageData.phoneNumber != "" || this.errorMessageData.messageBody != "") {
        return;
      }

      //
      this.status = 1;
    },
    pushSubmit : function() {
      this.$root.showLoad();

      let params = new URLSearchParams();
      params.append('name', this.name);
      params.append('mailAddress', this.mailAddress);
      params.append('phoneNumber', this.phoneNumber);
      params.append('body', this.messageBody);
      
      axios.post(this.$root.SERVER_PATH + 'contact', params)
      .then((response) => {
        this.$root.log(response.data);
        if (!this.$root.checkServerResponse(response, false)) {
          return;
        }

        this.$root.closePopup();
        this.status = 2;
      });
    },
    pushEdit : function() {
      this.status = 0;
    },
    pushTop : function() {
      this.$router.push("/");
    },
    initAction : function() {
      this.$root.closePopup();
    }
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      vm.$root.showLoad();
      vm.$root.getBaseInfo();
      vm.$root.setCurrentPage("");
      vm.initAction();
    });
  },
  beforeRouteUpdate(to, from, next) {
    next(vm => {
      vm.$root.showLoad();
      vm.$root.getBaseInfo();
      vm.$root.setCurrentPage("");
      vm.initAction();
    });
  }
}
</script>

