<template>
  <div class="registrationConfirmation" style="text-align:center; width:270px; margin:auto">
    <div style="height:36px" />
    <span class="p12">{{ title }}</span>
    <div style="height:20px" />
    <span class="p9">{{ message }}</span>
    <div style="height:38px" />
    <span class="btn" :class="this.$root.theme_bc_1" style="width:100%;" v-on:click="this.$root.pushHome"></span>
    <br>
    <br>
    <br>
    <br>
  </div>
</template>

<script>
import { getAuth, signOut } from 'firebase/auth'
import axios from 'axios'

export default {
  name: 'RegistrationConfirmation',
  components: {
  },
  data() {
    return {
      title : "",
      message : "",
    };
  },
  computed : {
  },
  methods : {
    initAction : function () {
      let tempId = this.$route.params.tempId;
      
      if (tempId == null || tempId.length != 20) {
        this.title = "";
        this.message = "URL";
        this.$root.closePopup();
        return;
      }

      let params = new URLSearchParams();
      params.append('tempId', tempId);

      axios.post(this.$root.SERVER_PATH + 'register', params)
      .then((response) => {
        let resData = response.data;
        console.log(resData);

        if (resData["status"] == this.$root.API_FAILED_NODATA || resData["status"] == this.$root.API_FAILED_PARAM) {
          //
          this.title = "";
          this.message = "URL";
        } else if (resData["status"] == this.$root.API_FAILED_DUPLICATE) {
          //
          this.title = "";
          this.message = "";
        } else if (resData["status"] == this.$root.API_FAILED_EXEPTION) {
          //
          this.title = "";
          this.message = "";
        } else {
          console.log("AAAAAAAAAA");

          if (resData["kbn"] == "NEW") {
            this.title = "";
            this.message = "\n";
          } else if (resData["kbn"] == "ADD") {
            this.title = "";
            this.message = "\n";
            this.$root.closePopup();
          } else if (resData["kbn"] == "CHANGE") {
            this.title = "";
            this.message = "\n";
          }

          //
          signOut(getAuth());
          this.$root.userData = {
            userId : "",
            userName : "",
            profile : "",
          };

          this.$root.isLogin = false;
        }
        
        this.$root.closePopup();
      }).catch((error) => {
        this.$root.comNetworkError(error);
      });
    },
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      vm.$root.showLoad();
      vm.initAction();
      vm.$root.setCurrentPage("");
    });
  },
  beforeRouteUpdate(to, from, next) {
    next(vm => {
      vm.$root.showLoad();
      vm.initAction();
      vm.$root.setCurrentPage("");
    });
  }
}
</script>

